import React from 'react';
import { GoogleLogout } from 'react-google-login';


const AdminView = ({ user, onLogout, setView }) => {
  const handleGenerateReport = () => {
    const programName = prompt('   ');
    if (programName === 'attendanceLog') {
      setView(programName)
    }else{
      alert('incorrect report name')
    }
  };

  const handleManagePrograms = () => {
    // Implement the logic to manage programs
  };

  const handleViewUsers = () => {
    // Implement the logic to view user data
  };

  return (
    <div className="container">
      <h1> </h1>
      <div className="space-y-4">
        <button onClick={handleGenerateReport} className="bg-primary"> </button>
        <button onClick={handleManagePrograms} className="bg-secondary"> </button>
        <button onClick={handleViewUsers} className="bg-tertiary">  </button>
      </div>
      <GoogleLogout
        clientId={process.env.REACT_APP_CLIENT_ID}
        buttonText=" "
        onLogoutSuccess={onLogout}
      />
    </div>
  );
};

export default AdminView;
